@import url(https://fonts.googleapis.com/css?family=Spartan:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.Settings{padding:0 20px;margin:15px 0}.Settings .options{margin:1.5rem 0 1.5rem 1.5rem}.Settings .options div{display:flex}.Settings .options div h6{margin-bottom:0;line-height:1.5;margin:0 1.5rem}

.About{padding:0 20px;margin:15px 0}

.CoolApps{padding:0 20px;margin:15px 0}

body{font-family:"Roboto", sans-serif}.bg-grey{background-color:#e3e3e3;padding:0}.pl-20{padding-left:20px}.dropdowns h5{line-height:1.3}.dropdowns h5:not(:first-child){margin-left:0.3rem}a:hover{text-decoration:none !important}.truncate{width:250px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}@media (max-width: 767px){.pl-sm-7{padding-left:7px}}@media (min-width: 768px){.pl-md-20{padding-left:20px}.pr-md-20{padding-right:20px}}.pr-20{padding-right:20px}

.color-nav{background:#c61414 !important}form{width:80% !important;position:relative}form:before{content:"\f3eb";font-weight:900;font-size:1.5em;display:block;font-family:"Font Awesome 5 Brands";position:absolute;top:50%;left:10px;z-index:9999;-webkit-transform:translateY(-50%);transform:translateY(-50%)}form input{width:100% !important;position:relative;padding-left:2.5rem !important}.custom-nav-link{display:flex;line-height:1;color:#212529;-webkit-transition:.3s all ease;transition:.3s all ease}.custom-nav-link:hover{color:#fff}.custom-nav-link span{margin-left:7px}.nav-item{margin-left:10px}.navbar-brand{width:70%}.navbar-brand img{width:100%}

.Loader{text-align:center;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.Loader h6{margin-top:1rem}

footer{-webkit-transition:.5s all ease-in-out;transition:.5s all ease-in-out}footer a{color:#000;display:flex;justify-content:space-around}footer a:hover{color:#c61414 !important}

.Dropdown button{font-size:14px;line-height:1.3;margin-left:5px;padding:2.3px 4.3px;background-color:#c61414 !important}

.Post{padding:0 20px;margin:15px 0}.Post .header{line-height:2}.Post .header h4{color:#5799b9;margin-right:.5em;margin-bottom:0;font-size:1.3rem}.Post .header span{color:#51839c}.Post .stats span{color:#51839c;position:relative;margin-right:5px}.Post .stats span:not(:first-child){margin:0 5px}.Post .stats span:not(:last-child):after{content:"|";display:block;position:absolute;top:50%;left:100%;-webkit-transform:translateY(-50%);transform:translateY(-50%);margin-left:3px}@media (max-width: 767px){.Post>div{display:block !important}.Post .stats span:not(:first-child){margin:0}.Post .stats span:after{content:'' !important}}@media (min-width: 768px) and (max-width: 991px){.Post .header h4{font-size:1.2rem}}

.Comment{padding:0 20px;margin:15px 0}.Comment .header{line-height:2}.Comment .header h5{color:#d57575;margin-right:.5em;margin-bottom:0;font-size:1.1em}.Comment .header span{color:#d57575}.Comment .stats span{color:#d57575;position:relative;margin-right:5px}.Comment .stats span:not(:first-child){margin:0 5px}.Comment .stats span:not(:last-child):after{content:"|";display:block;position:absolute;top:50%;left:100%;-webkit-transform:translateY(-50%);transform:translateY(-50%);margin-left:3px}

.btn-danger.resetStateDateBtn{margin:15px 20px;display:none}.btn-danger.resetStateDateBtn:last-child{display:block !important}.DateRangePicker{margin:15px 20px}

.Pagination{justify-content:center}.Pagination .page-link{-webkit-transition:0.5s all;transition:0.5s all;color:#5799b9}.Pagination .page-link:hover{cursor:pointer}.Pagination .active .page-link{background-color:#C61414;border-color:#C61414}.Pagination .active .page-link:hover{color:#C61414;background-color:#e9ecef;border-color:#dee2e6}

